<template>
  <el-container class="main-container">
    <!-- 头部区域 -->
    <el-header>
      <!-- 左侧的 logo -->
      <!-- <img src="../../assets/images/logo.png" alt="" /> -->
      <h1>这里是LOGO</h1>
      <!-- 右侧的菜单 -->
      <el-menu
        class="el-menu-top"
        mode="horizontal"
        background-color="#23262E"
        text-color="#fff"
        active-text-color="#409EFF"
      >
        <el-menu-item index="2" @click="logoutFn"
          ><i class="el-icon-switch-button"></i>退出</el-menu-item
        >
      </el-menu>
    </el-header>
    <el-container>
      <!-- 侧边栏区域 -->
      <el-aside width="200px">
        <!-- 左侧边栏的用户信息 -->
        <!-- 左侧边栏的用户信息 -->
        <div class="user-box">
          <img :src="user_pic" alt="" v-if="user_pic" />
          <img src="../../assets/images/defaultLOGO.jpg" v-else />
          <span>欢迎 {{ username }}</span>
        </div>
        <!-- 通过router开启导航路由模式 -->
        <el-menu
          :default-active="$route.path"
          class="el-menu-vertical-demo"
          background-color="#23262E"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          router
        >
          <!-- v-for渲染左侧菜单列表 -->
          <!-- 轮播图 -->
          <el-menu-item index="/swiperCMD">
            <i class="el-icon-picture"></i>
            <span slot="title">轮播图管理</span>
          </el-menu-item>
          <!-- 菜品推荐 -->
          <el-menu-item index="/suggesstionCMD">
            <i class="el-icon-position"></i>
            <span slot="title">菜品推荐管理</span>
          </el-menu-item>
          <!-- 公告 -->
          <el-menu-item index="/noticeCMD">
            <i class="el-icon-s-order"></i>
            <span slot="title">公告管理</span>
          </el-menu-item>
          <!-- 食堂管理 -->
          <el-menu-item index="/roomCMD">
            <i class="el-icon-map-location"></i>
            <span slot="title">食堂管理</span>
          </el-menu-item>
          <!-- 店铺管理 -->
          <el-menu-item index="/shopCMD">
            <i class="el-icon-food"></i>
            <span slot="title">店铺管理</span>
          </el-menu-item>
          <!-- 用户管理 -->
          <el-menu-item index="/userCMD">
            <i class="el-icon-user"></i>
            <span slot="title">用户管理</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <!-- 页面主体区域 -->
      <el-container>
        <!-- 二级路由挂载点 -->
        <el-main>
          <router-view></router-view>
        </el-main>
        <!-- 底部 footer 区域 -->
        <el-footer>© www.4399.com - 带给你快乐的网站</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "my-layout",
  // =============================================以下是Method=============================================
  methods: {
    // 退出登录
    logoutFn() {
      this.$confirm("是否确认退出？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          // 调用mutation方法,清除token串
          this.$store.commit("updateToken", "");
          // 清除userInfo
          this.$store.commit("updateUserInfo", {});
          //   跳转至登录页面
          this.$router.push("/login");
        })
        .catch((err) => err);
    },
  },
  computed: {
    ...mapGetters(["username", "user_pic"]),
  },
};
</script>

<style lang="less" scoped>
.main-container {
  height: 100%;
  .el-header,
  .el-aside {
    background-color: #23262e;
  }
  .el-header {
    padding: 0;
    display: flex;
    justify-content: space-between;
  }
  .el-main {
    overflow-y: scroll;
    height: 0;
    background-color: #f2f2f2;
  }
  .el-footer {
    background-color: #eee;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.avatar {
  border-radius: 50%;
  width: 35px;
  height: 35px;
  background-color: #fff;
  margin-right: 10px;
  object-fit: cover;
}
// 左侧边栏用户信息区域
.user-box {
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  user-select: none;
  img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: #fff;
    margin-right: 15px;
    object-fit: cover;
  }
  span {
    color: white;
    font-size: 12px;
  }
}
// 侧边栏菜单的样式
.el-aside {
  .el-submenu,
  .el-menu-item {
    width: 200px;
    user-select: none;
  }
}
</style>
